import { BaseClass } from '../base-class';
import { Exposure } from './exposure';
import { Security } from './security';
import { InnerRating } from './inner-rating';
import { BaseRating, RatingBrief } from './base-rating';

/**
 * @author taohui
 *
 * 
 */
export class Company extends BaseClass {
  id: number; // 
  name: string; // 
  foundDate: string; // 
  regDate: string; // 
  regAddress: string; // 
  officeAddress: string; // 
  officePostCode: number; // 
  regCapital: number; // 
  currency: string; // 
  legRepresent: string; // 
  chairman: string; // 
  gmanager: string; // 
  bsecretary: string; // 
  businScope: string; // 
  mainBusion: string; // 
  exposure: Exposure; // 
  orgFormId: number; // id
  orgFormName: string; // 
  securityList: Security[]; // 
  innerRating: InnerRating; // 
  outerRating: BaseRating; // 
  web: string; // 
  country: string; // 
  region: number; // 
  city: number; // 
  regionCode: number; // 
  companyPhone: number; // 
  companyFax: number; // 
  comanyEmail: string; // 
  blnumb: number; // 
  ntrnum: number; // 
  ltrnum: number; // 
  orgnum: number; // 
  srcCode: string; // 
  accountingFirm: string; // 
  legaladvisor: string; // 
  companyState: number; // 
  employNum: string; // 
  affilparties: AffilParty[]; // /
  ratingBriefVO: RatingBrief;

  generateByRaw(raw: any) {
    super.generateByRaw(raw);
    if (raw.exposure) {
      this.exposure = new Exposure();
      this.exposure.generateByRaw(raw.exposure);
    }
    if (raw.innerRating) {
      this.innerRating = new InnerRating();
      this.innerRating.generateByRaw(raw.innerRating);
    }
    if (raw.outerRating) {
      this.outerRating = new BaseRating();
      this.outerRating.generateByRaw(raw.outerRating);
    }
    this.securityList = [];
    if (raw.securityList && Array.isArray(raw.securityList) && raw.securityList.length > 0) {
      for (let item of raw.securityList) {
        let security = new Security();
        security.generateByRaw(item);
        this.securityList.push(security);
      }
    }
  }
}


export class CompanyRegion extends BaseClass {
  regionType: number;  // 
  regionCodeLevel1: number;
  regionNameLevel1: string; // 
  regionCodeLevel2: number;
  regionNameLevel2: string; // 
  regionCodeLevel3: number;
  regionNameLevel3: string; // 
}

export class ShareHolder extends BaseClass {
  endDate: string;
  endYear: string;
  endMonth: string;
  endDay: string;
  items: ShareHolderItem[];
  isPlus: boolean;
}
export class ShareHolderItem extends BaseClass {
  shareholderName: string; // 
  shareholderType: string; // 
  shareholderNum: number; // 
  shareholderRatio: number; // 
  shareType: string; // 
  changeAmount: string;
  status: number;
  changeRatio: number;
}

export class AffilParty extends BaseClass {
  affilParty: string; // /
  relationType: string; // affilParty
  inoShaRatio: number; // 
}



