import { BaseClass } from '../base-class';
import { PortfolioItem } from './portfolio-item';

export class Portfolio extends BaseClass {
  id: number; // id
  name: string; // 
  type: string; // 
  owner: string; // 
  remark: string; // 
  isDefault: number; // 
  isDynamic: number; //  01
  obsoleteDate: string;
  updateDate: string;
  createDate: string;
  portfolioItems: any[];
  addedItemIds: any[];
  removedItemIds: any[];
  companies: PortfolioItem[];
  regulationId: number;
  regulationName: string;
  regulationDesc: string;
  get text() {
    return this.name;
  }
  set text(value: any) {
    this.name = value;
  }
}





