import { BaseClass } from '../base-class';
export class Regulation extends BaseClass {
  id: number;
  name: string;
  desc: string;
}
export class RegulationCondition extends BaseClass {
  name: string;
  filterType: string; // 	01
  desc: string;	// 
  exposureId: any[]; // /
  marketNames: any[];	// /
  tradeMarket: any[];
  issueBonds: number;	//  10
  rating: any[];	// 
  creditRating: any[];	// 
  ratingType: any[];	//  
  couponRate: string[];	//  2,3.5 
  bondType: any[];	// 
  issueType: any[];	//  
  issueVolum: string; // 
  type: any[];
}



