import { Injectable } from '@angular/core';
import { Exposures } from './entities';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class ExposureService {
  private getExposureDetailApi = '/exposures/';
  constructor(private http: HttpClient) { }
  // 
  createExposure(item) {
    return this.http.post(`/exposures/exposure`, item);
  }
  // 
  deleteExposure(item) {
    return this.http.delete(`/exposures/${item.id}`);
  }
  // 
  saveExposure(item) {
    return this.http.put(`/exposures/${item.id}`, item);
  }
  // 
  getExposureDetail(id: number) {
    return this.http.get(this.getExposureDetailApi + `${id}/factorMapping`);
  }
  // -
  editExposureDetail(id: number, exposures: Exposures) {
    return this.http.put(this.getExposureDetailApi + `${id}/factorMapping`, exposures);
  }
  // 
  delExposureDetail(id: number, factorCode: number) {
    return this.http.delete(this.getExposureDetailApi + `${id}/factorMapping?factorCode=${factorCode}`);
  }
  // 
  addExposureDetail(id: number, exposures: Exposures) {
    return this.http.post(this.getExposureDetailApi + `${id}/factorMapping`, exposures);
  }
  // 
  setDefaultFactor(id: number, factorCode: string) {
    return this.http.put(this.getExposureDetailApi + `${id}/defaultFactorMapping/${factorCode}`, null);
  }
  cancelDefaultFactor(id: number, factorCode: string) {
    return this.http.delete(this.getExposureDetailApi + `${id}/defaultFactorMapping/${factorCode}`);
  }
}



